.Navbar .nav-text a:hover {
  background-color: #634d45;
}

.Navbar .nav-menu-items {
  width: 100%;
  padding: 4px;
  overflow-y: scroll;
}

.Navbar .navbar-toggle {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.Navbar span {
  margin-left: 16px;
}

/* sidebar toggle */

.Navbar .con,
.Navbar .special-con {
  cursor: pointer;
  display: inline-block;
}

.Navbar .bar {
  display: block;
  height: 5px;
  width: 50px;
  background: #ffffff;
  margin: 10px auto;
}

.Navbar .con {
  width: auto;
  margin: 0 auto;
  position: relative;
  right: 0.75rem;
  transition: all 0.7s ease;
}

.Navbar .con:hover .bar,
.special-con:hover .bar {
  background-color: #4c332b;
}

.Navbar .col {
  position: relative;
  left: 6px;
}

.Navbar .bar {
  transition: all 0.7s ease;
}

.Navbar .con:hover .arrow-top {
  transform: rotateZ(45deg) translateY(11px);
  width: 25px;
}

.con:hover .arrow-middle {
  transform: translateX(-25px);
}

.con:hover .arrow-bottom {
  transform: rotateZ(-45deg) translateY(-11px);
  width: 25px;
}

.con:hover .arrow-top-r {
  transform: rotateZ(-45deg) translateY(11px);
  width: 25px;
}

.con:hover .arrow-middle-r {
  transform: translateX(25px);
}

.con:hover .arrow-bottom-r {
  transform: rotateZ(45deg) translateY(-11px);
  width: 25px;
}

.con:hover .arrow-top-fall {
  transform: translateY(15px);
}

.con:hover .arrow-bottom-fall {
  transform: translateY(-15px);
}

.special-con {
  margin: 0 auto;
  transition: all 0.7s ease;
}

.special-con:hover .arrow-top-fall {
  transition: all 0.7s ease-in-out;
  transform: translateY(15px);
}

.arrow-bottom-fall,
.arrow-top-fall {
  transition: all 0.7s ease-in-out;
}

.special-con:hover .arrow-bottom-fall {
  transform: translateY(-15px);
  transition: all 0.7s ease-in-out;
}

.Navbar .nav-image-name {
  margin-top: 13px;
  color: white;
  font-weight: bold;
  text-align: center;
}
.Navbar .nav-image-main img {
  border-radius: 50%;
  transform: scale(1.1);
  border: 2px groove rgb(235, 221, 197);
}

